import React, { Component } from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';
import Form from './styles/Form';
import formatMoney from '../lib/formatMoney';

class CreateItem extends Component {
    state ={
        title:'',
        description:'',
        image:'',
        largeImage:'',
        price:0
    };
    handleChange = (e) =>{
        const {name, value} = e.target;
        this.setState({[name]:value})
    }

    render() {
        return (
            <Form>
                <fieldset>
                    <h1>Sell an Item.</h1>
                    <label htmlFor="image">
                    Image
                    <input type="file" id="image" name="image" placeholder=""/>
                    </label>
                    <label htmlFor="title">
                    Title
                    <input
                    value={this.state.title}
                    onChange={this.handleChange}
                    type="text"
                    id="title"
                    name="title"
                    placeholder="title"
                    required
                    />
                    </label>
                    <label htmlFor="price">
                    Price {0}
                    <input type="text" id="price" name="price" placeholder="0"  />
                    </label>
                    <label htmlFor="description">
                    <textarea type="text" id="description" name="description" placeholder="Description of this item" />
                    </label>
                </fieldset>

            </Form>
        );
    }
}

export default CreateItem;